export default {
    onShow(){
        this.getpdpaypwd()
    },
    data(){
        return {
            zhifupwd:0
        }
    },
    methods: {
        getpdpaypwd(){
            this.$http.getpdpaypwd().then(res=>{
                if(res.status == 200) {
                    this.zhifupwd = res.data.zhifupwd
                }
            })
        }
    },
}
